import React, { useState, useEffect } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faStrikethrough } from '@fortawesome/free-solid-svg-icons'

import Dropdown from 'react-bootstrap/Dropdown'
import DropdownButton from 'react-bootstrap/DropdownButton'
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import InputGroupBtn from './InputGroupBtn'
import { updateEntities } from '../../Utility/API'

export default function DetailSetting(props) {
    const [activeStatus, setActiveStatus] = useState();

    const onChangeActiveStatus = (typeVal, newStatusVal) => {
        let finalData = data.map((arr) => {
            if (arr['value'] == typeVal) {
                arr['is_active'] = newStatusVal;
                setActiveStatus(newStatusVal);
            }
            return arr;
        });
        let type = props.head;
        let params = [{
            "_active": newStatusVal,
            "previous_type": type,
            "updated_type": type,
            "previous_value": typeVal,
            "updated_value": typeVal
        }];
        //call the API
        updateEntities(props.cid, props.did, params).then((data) => {
            console.log("values updated", data);
            setData(finalData);
        })
       
    }

    const onEditReq = (typeVal, newVal) => {
        let finalData = data.map((arr) => {
            if (arr['value'] == typeVal) {
                arr['user_modified'] = newVal;
            }
            return arr;
        });

        setData(finalData);
    }

    const onSave = (oldType, newType, oldVal, newVal, status) => {
        //call the API to update the entities
        let params = [{
            "_active": status,
            "previous_type": oldType,
            "updated_type": newType,
            "previous_value": oldVal,
            "updated_value": newVal
        }]
        updateEntities(props.cid, props.did, params).then((data)=>{
            console.log("values updated", data);
        })
    }

    const [data, setData] = useState([]);
    useEffect(() => {
        setData(props.val);
    }, []);


    return (
        <tbody>
            <tr><th>{props.head}</th></tr>

            {data.map((val) => (
                <tr className="detail-setting">
                    <td>
                        <div className={val['user_modified'] ? 'hide' : 'show'}>
                            <span className={`text ${!val['is_active'] ? 'line-through' : ''}`}>{val.value}</span>
                            <span className="float-right">
                                <DropdownButton as={ButtonGroup} title={''}>
                                    {(val['is_active']) ?
                                        <Dropdown.Item eventKey="1" onClick={() => onChangeActiveStatus(val.value, false)}>
                                            <FontAwesomeIcon icon={faStrikethrough} />
                                            Disable
                                        </Dropdown.Item>
                                        :
                                        <Dropdown.Item eventKey="3" onClick={() => onChangeActiveStatus(val.value, true)}>
                                            <FontAwesomeIcon icon={faStrikethrough} />
                                            Enable
                                        </Dropdown.Item>
                                    }


                                    <Dropdown.Item eventKey="2" onClick={() => onEditReq(val.value, true)}>
                                        <FontAwesomeIcon icon={faEdit} />
                                    Edit
                                </Dropdown.Item>
                                </DropdownButton>
                            </span>
                        </div>
                        <div className={val['user_modified'] ? 'show' : 'hide'}>
                            <InputGroupBtn activestatus={val['is_active']} type={props.head} val={val.value} save={onSave} click={() => onEditReq(val.value, false)} />
                        </div>
                    </td>
                </tr>
            ))}
        </tbody>
    );
}