import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFilter } from '@fortawesome/free-solid-svg-icons'

export default function FilterInFoText(props) {
    return (<div className="filter-text">

        {
            Object.keys(props.data).map((key) => (
                <div key={key} className={props.data[key].length > 0?'show':'hide'}>
                    <FontAwesomeIcon icon={faFilter} />  &nbsp;
                    <b className="head">{key}</b> &nbsp; &nbsp;
                    <span className="body-text">{props.data[key].length > 0 ? props.data[key].join(", ") : ""}</span>
                </div>
            ))
        }
    </div>)
}