function getRoutes() {
    return {
        ROUTE: {
            COLLECTIONS: 'collections',
            DETAILS: 'details'
        }
    }
}

function getUrl() {
    return {
        URL: {
            COLLECTIONS: {
                GET: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5000/api/collections',
                KEY: 'collections'
            },
            FILTERS: {
                GET: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5002/dexninja_api/collections/$id/filters',
                KEY: 'filters'
            },
            DOCUMENT: {
                GET: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5002/dexninja_api/collections/$cid/documents/$did',
                POST: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5002/dexninja_api/collections/$cid/documents',
                KEY: 'document',
            },
            DOCUMENTS: {
                GET: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5002/dexninja_api/collections/$id/documents?size=20',
                KEY: 'documents'
            },
            ENTITIES: {
                GET: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5002/dexninja_api/collections/$cid/documents/$did/entities',
                PUT: 'http://ec2-35-176-120-96.eu-west-2.compute.amazonaws.com:5002/dexninja_api/collections/$cid/documents/$did/entities',
                KEY: 'entities'
            }
        }
    }
}

export {
    getRoutes,
    getUrl
}