import React, { useEffect, useState } from 'react'
import FormControl from 'react-bootstrap/FormControl'
import InputGroup from 'react-bootstrap/InputGroup'
import Button from 'react-bootstrap/Button'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheck } from '@fortawesome/free-solid-svg-icons'

export default function InputGroupBtn(props) {
    const [stateData, setStateData] = useState({
        type: props.type,
        value: props.val
    });

    const save = () => {
        let type = stateData.type;
        let value = stateData.value;
        props.click();
        if(props.type == type && props.val == value){
            console.log("values are same");
            return false;
        }
       
        props.save(props.type, type, props.val, value, props.activestatus);
    }

    return (<InputGroup className="mb-3 input-btn input-btn-comp">
        <FormControl
            defaultValue={props.type}
            onChange={(e) => setStateData({ ...stateData, type: e.target.value })}
            aria-label={props.type}
        />
        <FormControl
            defaultValue={props.val}
            onChange={(e) => setStateData({ ...stateData, value: e.target.value })}
            aria-label={props.val}
        />
        <InputGroup.Append>
            <Button variant="outline-secondary" className="border-1-gray" onClick={save}>
                <FontAwesomeIcon icon={faCheck} />
            </Button>
        </InputGroup.Append>
    </InputGroup>);
}