import React from "react"
import DetailSetting from './DetailSetting'

export default function DocumentSetting(props) {

    /* based on the data, create setting object */
    let settings = {};
    props.data.map((val) => {
        if (!settings[val['type']]) {
            settings[val['type']] = [];
        }
        settings[val['type']].push(val)
    });

    const loop = Object.keys(settings).map((key) => (
        <DetailSetting cid={props.cid} did={props.did} head={key} val={[...settings[key]]} />
    ));

    return (
        <>
            {loop}
        </>
    );
}