import React, { useEffect, useState, useCallback } from 'react'
import UseBaseContext from '../../ContextApi/UseBaseContext'
import Accordion from 'react-bootstrap/Accordion'
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import { getDocuments } from '../../Utility/API'

export default function AccordionCard(props) {
    /* get the context instance */
    const { setData, getData } = UseBaseContext();

    const [checkedData, setCheckedData] = useState({});

    const onCheckBoxChange = (e) => {
        let isChecked = e.target.checked;
        let parent = e.target.name;
        let val = e.target.id;

        let selectedData = getData('filter-data') || {};

        if (isChecked) {
            if (!selectedData.hasOwnProperty(parent))
                selectedData[parent] = [];

            setData({
                ...selectedData,
                [parent]: [...selectedData[parent], val]
            },
                'filter-data', applyFilters);
        } else {
            setData({
                ...selectedData,
                [parent]: selectedData[parent].filter(inst => inst != val)
            },
                'filter-data', applyFilters);
        }
    }

    const applyFilters = (contextData) => {
        // checkedData;
        let id = getData('selectedCollectionId');
        let filterData = contextData['filter-data'];
        let arr = [];

        setCheckedData(filterData);
        Object.keys(filterData).map((key) => {
            filterData[key].map((val) => {
                arr.push(`${key}:${val}`);
            })
        })
        let finalStr = '&filter=' + arr.join(',');
        getDocuments(id, finalStr).then((data) => {

            //setting the data in the global context
            setData(data, 'documents');
        });
    }

    return (
        <Card>
            <Accordion.Toggle as={Card.Header} eventKey={props.event}>
                {props.head}
                <b className="float-right">
                    {JSON.stringify()}
                    {checkedData.hasOwnProperty(props.head) ? checkedData[props.head].length || '' : ''}
                </b>
            </Accordion.Toggle>
            <Accordion.Collapse eventKey={props.event}>
                <Card.Body>

                    <Form.Group id="formGridCheckbox">
                        {props.checks.map((val) => (
                            <Form.Check custom
                                key={val}
                                onChange={onCheckBoxChange}
                                name={props.head}
                                id={val}
                                type="checkbox"
                                label={val} />
                        ))}
                    </Form.Group>
                </Card.Body>
            </Accordion.Collapse>
        </Card>
    );
}
