import React from 'react';
import './App.css';
import Header from './Pages/Header'
import Home from './Pages/Home'
import Details from './Pages/Details'
import Collections from './Pages/Collections'
import { getRoutes } from './Utility/Config'

import {
  BrowserRouter as Router,
  Route,
  Switch,
} from 'react-router-dom';

import { BaseContextProvider } from './ContextApi/BaseContext'

function App() {
  const CONFIG = getRoutes();

  return (
    <div className="App">
      <BaseContextProvider>
        <Header />
        <Router>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path={`/${CONFIG.ROUTE.COLLECTIONS}`} component={Collections} />
            <Route exact path={`/${CONFIG.ROUTE.COLLECTIONS}/:cid/${CONFIG.ROUTE.DETAILS}/:did`} component={Details} />
          </Switch>
        </Router>
      </BaseContextProvider>
    </div>
  );
}

export default App;
