import React, { useEffect, useState } from 'react'
import { Document, Page } from 'react-pdf/dist/entry.webpack';


export default function PDFViewer(props) {
    const [state, setState] = useState({ page: 1 });

    const onDocumentLoadSuccess = ({ numPages }) => {
        setState({ ...state, totalPage: numPages });
    }

    const nextClick = () => {
        let pg = state.page;
        setState({ ...state, page: ++pg });
    }

    const prevClick = () => {
        let pg = state.page;
        setState({ ...state, page: --pg });
    }


    return (<div className="document-part">
        <div className="pdf-control">
            <a className={state.page == state.totalPage ? 'hide' : 'show'} onClick={nextClick}>Next</a> &nbsp;&nbsp; <a className={state.page == 1 ? 'hide' : 'show'} onClick={prevClick}>Previous</a>
        </div>
        <Document
            onLoadSuccess={onDocumentLoadSuccess}
            file={props.document}>
            <Page width={window.innerWidth * (8.5 / 12)} pageNumber={state.page} />
        </Document>
    </div>);
}