import React, { useEffect, useState } from 'react'
import UseBaseContext from './../ContextApi/UseBaseContext'
import Form from 'react-bootstrap/Form'

import AccordionCard from '../Components/Project/AccordionCard'
import DocumentSetting from '../Components/Project/DocumentSetting'
import Box from '../Components/Project/Box'

import { getDocument, get, getEntities } from '../Utility/API'
import PDFViewer from '../Components/Project/PDFViewer'

function Details(props) {
    /* get the context instance */
    const { getData } = UseBaseContext();
    const [document, setDocument] = useState({});
    const [entityData, setEntityData] = useState([]);
    const [docData, setDocData] = useState('');
    const [viewType, setViewType] = useState('pdf');
    const [textFile, setTextFile] = useState('');

    const collectionId = props.match.params.cid;
    const documentId = props.match.params.did;

    if (!documentId) {
        console.error("ERROR OCCURRED! Go to home page");
    }

    const setDocumentUrl = (data) => {
        let links = data._links;
        if (!links || links.length == 0)
            return '';

        //plain text
        let link = links.find((link) => link.rel == 'text');
        get(link.href).then((resp) => {
            setTextFile(resp.data);
        })

        //document
        link = links.find((link) => link.rel == 'file');
        setDocData(link.href);

    }
    /**
     * get the documents from already saved in context API (saving an API call)
     */
    const init = () => {
        let data = getData('documents');
        if (!data || data.length == 0) {
            //make an API call 
            getDocument(collectionId, documentId).then((data) => {
                setDocument(data);
                setDocumentUrl(data);
            });
        } else {
            let d = data.find((v) => v.id == documentId);
            setDocument(d);
            setDocumentUrl(d);
        }
    }

    const setEntities = () => {
        getEntities(collectionId, documentId).then((data) => {
            setEntityData(data);
        });
    }

    const onChangeView = (e) => {
        let viewType = e.target.value;
        setViewType(viewType);
    }

    useEffect(() => {
        init();
        setEntities();
    }, []);

    let data = ['Loc', 'Invoice Date', 'Billing Cycle', 'Total', 'FAC', 'Invoice', 'Previous Balance'];
    let loop = data.map((val, index) => (
        <AccordionCard head={val} event={index}>
            <Form.Group id="formGridCheckbox">
                <Form.Check type="checkbox" label="check box #1" />
                <Form.Check type="checkbox" label="check box #2" />
                <Form.Check type="checkbox" label="check box #3" />
            </Form.Group>
        </AccordionCard>
    ));
    /* all of the main content goes here  */
    return (
        <section id="detail-pg" className="m-2-b custom">
            <div className="row text-l m-l-r-10">
                <div className="col-lg-9 pdf-view">
                    <Box head="Document" solid={true} type="primary" className="box">
                        <div className="text-data">
                            <div className={viewType == 'pdf' ? 'show' : 'hide'}>
                                <PDFViewer document={docData} />
                            </div>
                            <div className={viewType == 'text' ? 'show' : 'hide'}>
                                {textFile}
                            </div>
                        </div>
                    </Box>
                </div>
                <div class="col-lg-3 setting">
                    <Box head="Settings" solid={true} type="primary">
                        <Form.Group controlId="formGridState">
                            <Form.Control as="select" onChange={onChangeView}>
                                <option value='pdf'>PDF</option>
                                <option value='text'>TEXT</option>
                            </Form.Control>
                        </Form.Group>

                        <table size="sm" className="table p-5-tbl setting-tbl ">
                            <DocumentSetting cid={props.match.params.cid} did={props.match.params.did} data={entityData} />
                        </table>
                    </Box>
                </div>
            </div>

        </section>
    );
}

export default Details;