import Axios from 'axios';
import UseBaseContext from '../ContextApi/UseBaseContext'
import { getUrl } from './Config'

const CONFIG = getUrl();
const CORS_WORKAROUND = '';//'https://cors-anywhere.herokuapp.com/';
// const {setData, getData} = UseBaseContext();

function getCollections() {
    let url = CORS_WORKAROUND + CONFIG.URL.COLLECTIONS.GET;
    return new Promise((res, rej) => {
        get(url).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });

}

function getFilters(id) {
    let url = CORS_WORKAROUND + CONFIG.URL.FILTERS.GET;
    url = url.replace('$id', id);
    return new Promise((res, rej) => {
        get(url).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });

}

function getDocuments(id, param) {
    param = param || '';
    let url = CORS_WORKAROUND + CONFIG.URL.DOCUMENTS.GET + param;
    url = url.replace('$id', id);
    return new Promise((res, rej) => {
        get(url).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });
}

function getEntities(cid, did) {
    let url = CORS_WORKAROUND + CONFIG.URL.ENTITIES.GET;
    url = url.replace('$cid', cid).replace('$did', did);
    return new Promise((res, rej) => {
        get(url).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });

}

function getDocument(cid, did) {
    let url = CORS_WORKAROUND + CONFIG.URL.DOCUMENT.GET;
    url = url.replace('$cid', cid).replace('$did', did);
    return new Promise((res, rej) => {
        get(url).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });

}

function uploadDoc(cid, fileData) {
    let url = CORS_WORKAROUND + CONFIG.URL.DOCUMENT.POST;
    url = url.replace('$cid', cid);
    return new Promise((res, rej) => {
        post(url, fileData).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });
}

function updateEntities(cid, did, param){
    let url = CORS_WORKAROUND + CONFIG.URL.ENTITIES.PUT;
    url = url.replace('$cid', cid).replace('$did', did);
    return new Promise((res, rej) => {
        put(url, param).then((resp) => {
            let data = resp.data;
            res(data);

        }, (e) => {
            rej(e);
        });
    });

}

function get(url) {
    if (!url) {
        console.error("URL is a mandatory field to make an API call");
        return;
    }

    return new Promise((resolve, reject) => {
        Axios.get(url).then((data) => {
            resolve(data);
        }, (e) => {
            reject(e);
        });
    });
}

function post(url, param) {
    if (!url) {
        console.error("URL is a mandatory field to make an API call");
        return;
    }

    return new Promise((resolve, reject) => {
        Axios.post(url, param).then((resp) => {
            resolve(resp);
        }, () => {
            reject();
        });
    });

}

function put(url, param) {
    if (!url) {
        console.error("URL is a mandatory field to make an API call");
        return;
    }

    return new Promise((resolve, reject) => {
        Axios.put(url, param).then((resp) => {
            resolve(resp);
        }, () => {
            reject();
        });
    });
}


