import React from 'react'

export default function Box(props) {

    return (
        <div className={`box box-${props.type || 'primary'} ${props.solid ? 'box-solid' : ''} ${props.css}`}>
            {/* <div className="box-header with-border pull-left">
                <h3 className="box-title">{props.head}</h3>
            </div> */}

            <div className="box-body">
                {props.children}
            </div>
        </div>
    );
}
