import React, { useEffect, useState } from 'react'
import UseBaseContext from './../ContextApi/UseBaseContext'
import Tile from '../Components/Theme/Tile'
function Home() {
    /* get the context instance */
    const { getData } = UseBaseContext();
    var data = getData('dashboard');

    var loop = data.map((val) => (
        <Tile
            key={val.name}
            link={val.link}
            icon={val.icon}
            name={val.name}
            color={val.color} />
    ));
    /* all of the main content goes here  */
    return (
        <section id="home-pg" className="m-10-b">
            <div className="container">
                <div className="row">
                    {loop}
                </div>
            </div>
        </section>
    );
}

export default Home;