import React, { useEffect, useState } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFilePdf, faEye, faTimes, faDownload } from '@fortawesome/free-solid-svg-icons'
import {
    useHistory
} from "react-router-dom";

export default function TableCol(props) {
    const history = useHistory();
    return (
        <>
            {props.data.map((data) => (
                <div className="single-row" key={data.id}>
                    <div className="icon">
                        <FontAwesomeIcon icon={faFilePdf} />
                    </div>
                    <div className="row pointer" onClick={() => history.push(`/collections/${props.cid}/details/${data.id}`)}>
                        <div className="col-lg-12"> {data.name}</div>
                        <div className="col-lg-12"> <b>{data.type}</b> |  {data.created_on}</div>
                    </div>
                    <div className="toolbar">
                    <span className="action-btn"><FontAwesomeIcon icon={faTimes} /></span>
                    <span className="action-btn"><FontAwesomeIcon icon={faDownload} /></span>
                    </div>
                </div>
                // <tr key={data.id}>
                //     <td><FontAwesomeIcon icon={faFilePdf} /></td>
                //     <td width="50%">
                //         <div className="pointer" onClick={() => history.push(`/collections/${props.cid}/details/${data.id}`)}>
                //             {data.name}
                //         </div>
                //     </td>
                //     <td>{data.type}</td>
                //     <td>{data.created_on}</td>
                //     <td>
                //         <div>
                //             <span className="action-btn"><FontAwesomeIcon icon={faTimes} /></span>
                //             <span className="action-btn"><FontAwesomeIcon icon={faDownload} /></span>
                //         </div>
                //     </td>
                // </tr>
            ))}
        </>

    );
}