import React, { useState } from 'react';
import { faFile, faFolder, faCogs, faUsers } from '@fortawesome/free-solid-svg-icons'
import { getRoutes } from '../Utility/Config'

const BaseContext = React.createContext([{}, () => { }]);
const CONFIG = getRoutes();
const BaseContextProvider = (props) => {
    const [state, setState] = useState({
        dashboard: [{
            id: 1,
            name: 'Manage Collections',
            icon: faFolder,
            color: 'bg-aqua',
            link: CONFIG.ROUTE.COLLECTIONS
        },
        {
            id: 2,
            name: 'Manage Documents',
            icon: faFile,
            color: 'bg-green',
            link: CONFIG.ROUTE.COLLECTIONS
        }, {
            id: 3,
            name: 'Manage Configurations',
            icon: faCogs,
            color: 'bg-yellow',
            link: CONFIG.ROUTE.COLLECTIONS
        }, {
            id: 4,
            name: 'Manage Users',
            icon: faUsers,
            color: 'bg-red',
            link: CONFIG.ROUTE.COLLECTIONS
        }]
    });

    return (
        <BaseContext.Provider value={[state, setState]}>
            {props.children}
        </BaseContext.Provider>
    );
};

export { BaseContext, BaseContextProvider };