import React, { useEffect, useState } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCog, faArrowAltCircleRight } from '@fortawesome/free-solid-svg-icons'
import {
    BrowserRouter as Router,
    useHistory
} from "react-router-dom";
export default function Tile(props) {
    const history = useHistory();

    return (
        <div className="col-lg-3 col-xs-6">
            <div className={`small-box ${props.color}`}>
                <div className="inner">
                    <p>{props.name}</p>

                    {/* <p>New Orders</p> */}
                </div>
                <div className="icon">
                    <FontAwesomeIcon icon={props.icon} />
                </div>
                <a href="#" className="small-box-footer" onClick={(item) => {
                    history.push(`/${props.link}`);
                }}>
                    More info <FontAwesomeIcon icon={faArrowAltCircleRight} />
                </a>
            </div>
        </div>
    );
}
