import React, { useEffect, useState } from 'react'
import UseBaseContext from './../ContextApi/UseBaseContext'
import Accordion from 'react-bootstrap/Accordion'
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import Table from 'react-bootstrap/Table'
import Button from 'react-bootstrap/Button'

import AccordionCard from '../Components/Project/AccordionCard'
import Box from '../Components/Project/Box'
import TableCol from '../Components/Project/TableCol'
import FilterInFoText from '../Components/Project/FilterInFoText'

import { getCollections, getFilters, getDocuments, uploadDoc } from '../Utility/API'
import { checkAndSetData } from '../Utility/Utility'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUpload } from '@fortawesome/free-solid-svg-icons'

function Collections() {
    /* get the context instance */
    const { setData, getData } = UseBaseContext();

    /* managing state */
    const [collectionData, setCollectionData] = useState([]);
    const [filterData, setFilterData] = useState([]);
    const [selectedData, setSelectedData] = useState({});
    const [inputRef, setInputRef] = useState('');
    const [uploadedFile, setUploadedFile] = useState('');

    //////////////////////////////////////////////////
    /* place for all functions */
    //@id optional 
    function init(id) {
        setCollections(id).then((selectedId) => {

            setFilters(selectedId);
            setDocuments(selectedId);
        })
    }

    const setCollections = (selectedId) => {
        return new Promise((res, rej) => {
            // let isDataAvail = checkAndSetData('collections', getData, setCollectionData);
            // if (isDataAvail)
            //     res(getData('collections'));

            //call the api if the data is not available 
            getCollections().then((data) => {

                if (!data || data.length == 0)
                    return;

                let firstCollection = data[0];
                let id = selectedId || firstCollection.id;

                //set the data to state
                setSelectedData({ ...selectedData, collection: id });
                setCollectionData(data);

                //save the data to context
                setData(id, 'selectedCollectionId');
                setData(data, 'collections');

                res(id)
            });
        });

    }

    const setFilters = (id) => {
        id = id || selectedData.collection
        getFilters(id).then((data) => {
            setFilterData(data);
            setData(data, 'filters');
        });
    }

    const setDocuments = (id) => {
        id = id || selectedData.collection;
        // let isDataAvail = checkAndSetData('documents', getData, setDocListData);
        // if (isDataAvail && !isNewRequest)
        //     return true;
        getDocuments(id).then((data) => {

            //setting the data in the global context
            setData(data, 'documents');
        });
    }

    const changeCollection = (e) => {
        setSelectedData({ ...selectedData, collection: e.target.value });
        setData(e.target.values, 'selectedCollectionId');
        init(e.target.value);
    }

    const onFileChange = (e) => {
        let file = e.target.files[0];
        const formData = new FormData();
        formData.append("cid", selectedData.collection);
        formData.append(
            "files",
            file,
            file.name
        );

        //call the API
        uploadDoc(selectedData.collection, formData).then(() => {
            setDocuments(selectedData.collection);
            setFilters(selectedData.collection);
        })
    }

    const onFileUpload = () => {
        inputRef.click();
    }
    //////////////////////////////////////////////////

    useEffect(() => {
        init();
    }, []);


    //loop
    let filterLoop = [];
    if (filterData && filterData.length > 0) {
        filterLoop = filterData.map((data, index) => (
            <AccordionCard key={data.label} head={data.label} event={index} checks={data.keywords}></AccordionCard>
        ));
    }

    /* all of the main content goes here  */
    return (
        <section id="home-pg" className="custom">
            <div className="row text-l m-l-r-10">
                <div className="col-lg-3">
                    {/* <Box head="Select Collections" solid={true} type="primary"> */}
                    <small>Select Collection</small>
                    <Form.Group md="4" controlId="formGridState">
                        <Form.Control as="select" onChange={changeCollection}>
                            {
                                collectionData.map((data) => (
                                    <option value={data.id} key={data.name}>{data.name}</option>
                                ))
                            }
                        </Form.Control>
                    </Form.Group>
                    {/* </Box> */}
                </div>
                <div className="col-lg-9">
                    <div className="filter-element">
                        <div className="opt">
                            <small>Order By</small>
                            <Form.Group md="3" className="w-240" controlId="formGridState">
                                <Form.Control as="select" onChange={changeCollection}>
                                    <option value={1} key={1}>{"Date Modified"}</option>
                                </Form.Control>
                            </Form.Group>
                        </div>
                        <div className="opt-upload-btn">
                            <input type="file" ref={inp => setInputRef(inp)} className="hide" onChange={onFileChange} />
                            <Button variant="primary" onClick={onFileUpload}>
                                <FontAwesomeIcon icon={faUpload} />
                                    &nbsp; Upload
                                </Button>{' '}
                        </div>
                    </div>
                </div>
                <div className="col-lg-3">
                    <Box head="Filters" solid={true} type="primary">
                        <Accordion defaultActiveKey="0">
                            {filterLoop}
                        </Accordion>
                    </Box>
                </div>

                <div className="col-lg-9">
                    <Box head="Document List" solid={true} type="primary">
                        <FilterInFoText data={getData('filter-data') || {}} />
                        <div className="doc-list">
                            <TableCol cid={selectedData.collection} data={getData('documents') || []} />
                        </div>
                        {/* <Table bordered hover size="sm">
                            <TableCol cid={selectedData.collection} data={getData('documents') || []} />
                        </Table> */}
                    </Box>
                </div>
            </div>

        </section>
    );
}

export default Collections;